// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
//.options
import Vue from 'vue'
import App from './App'
import router from './router'
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
import axios from 'axios'  //ajax
import fontSize from './assets/fontSize.js' //js
import cookie from './assets/cookie.js'  //cookie.js
import './assets/css/reset.css' //
  import { MessageBox } from 'mint-ui';
  

Vue.use(MintUI);

Vue.prototype.$http= axios
Vue.prototype.$cookie= cookie
Vue.config.productionTip = false

//
//Vue.prototype.$url= '/platform/api/'; //
//Vue.prototype.$url= 'http://106.75.99.126:6302/platform/api/'; //
//Vue.prototype.$url= 'http://192.168.1.244:8093/platform/api/'; //
Vue.prototype.$url= 'http://106.75.99.126:6302/platform/api/'; //()

// 
axios.interceptors.request.use(function (config) {
    // 
    var token = cookie.getCookie('token');
    if(token !== null || token !== ""){
    	config.headers['X-Nideshop-Token'] = token
    }
    config.headers['Content-Type'] = 'application/json;charset=UTF-8';
    return config;
  }, function (error) {
    // 
    return Promise.reject(error);
  });

// 
axios.interceptors.response.use(function (response) {
    // 
    /**/
   if(response.data.errmsg == "" || response.data.errno ==401 ){
   				cookie.delCookie('userId');
    			cookie.delCookie('userInfo');
    			cookie.delCookie('token');
				function yzUserMessage(userMessage){
					if(cookie.getCookie(userMessage) == undefined || cookie.getCookie(userMessage) == null || cookie.getCookie(userMessage) == ''){
						return true;
					}else{
						return false;
					}
				}
	    	if(yzUserMessage('userId') && yzUserMessage('userInfo')){
			    	MessageBox({
						  title: ' ',
						  message: ' ',
						  showCancelButton: true
						},function(params){
								if(params == 'confirm'){
										router.push('/pages/register/register');
								}
						});
					}
		}
    return response;
  }, function (error) {
    // 
    return Promise.reject(error);
  });



/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



