import Vue from 'vue'
import Router from 'vue-router'
import home from '@/pages/home'  //
import classification from '@/pages/classification'  //
import shoppingcar from '@/pages/shoppingcar'  //
import ucenter from '@/pages/ucenter'  //
import search from '@/pages/search/search'  //-
import order from '@/pages/ucenter/order'  //-
import coupon from '@/pages/ucenter/coupon'  //-
import collect from '@/pages/ucenter/collect'  //-
import footprint from '@/pages/ucenter/footprint'  //-
import addressList from '@/pages/ucenter/addressList'  //-
import accountSecurity from '@/pages/ucenter/accountSecurity'  //-
import helpCenter from '@/pages/ucenter/helpCenter'  //-
import feedback from '@/pages/ucenter/feedback'  //-
import mobile from '@/pages/ucenter/mobile'  //-
import categoryPages from '@/pages/category/category'  //-
import goods from '@/pages/category/goods'  //
import brandDetail from '@/pages/category/brandDetail'  //
import orderDetail from '@/pages/ucenter/orderDetail'  //
import checkout from '@/pages/category/checkout'  //
import addressAdd from '@/pages/category/addressAdd'  //
import payResult from '@/pages/category/payResult'  //
import selCoupon from '@/pages/category/selCoupon'  //
import addressList2 from '@/pages/category/addressList'  //
import addressAdd2 from '@/pages/ucenter/addressAdd'  //
import amountMoney from '@/pages/ucenter/amountMoney'  //
import pay from '@/pages/category/pay'  //
import register from '@/pages/register/register'  //
import ptfwxy from '@/pages/xieyi/ptfwxy'  //

Vue.use(Router)

// /  component  home  vue 
export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },{
      path: '/pages/classification',
      name: 'classification',
      component: classification
    },{
      path: '/pages/shoppingcar',
      name: 'shoppingcar',
      component: shoppingcar
    },{
      path: '/pages/ucenter',
      name: 'ucenter',
      component: ucenter
    },{
      path: '/pages/ucenter/order',
      name: 'order',
      component: order
    },{
      path: '/pages/ucenter/coupon',
      name: 'coupon',
      component: coupon
    },{
      path: '/pages/ucenter/collect',
      name: 'collect',
      component: collect
    },{
      path: '/pages/ucenter/footprint',
      name: 'footprint',
      component: footprint
    },{
      path: '/pages/ucenter/addressList',
      name: 'addressList',
      component: addressList
    },{
      path: '/pages/ucenter/accountSecurity',
      name: 'accountSecurity',
      component: accountSecurity
    },{
      path: '/pages/ucenter/helpCenter',
      name: 'helpCenter',
      component: helpCenter
    },{
      path: '/pages/ucenter/feedback',
      name: 'feedback',
      component: feedback
    },{
      path: '/pages/ucenter/mobile',
      name: 'mobile',
      component: mobile
    },{
      path: '/pages/ucenter/search',
      name: 'search',
      component: search
    },{
      path: '/pages/category/category',
      name: 'categoryPages',
      component: categoryPages
    },{
      path: '/pages/goods/goods',
      name: 'goods',
      component: goods
    },{
      path: '/pages/category/brandDetail',
      name: 'brandDetail',
      component: brandDetail
    },{
      path: '/pages/ucenter/orderDetail',
      name: 'orderDetail',
      component: orderDetail
    },{
      path: '/pages/category/checkout',
      name: 'checkout',
      component: checkout
    },{
      path: '/pages/category/addressAdd',
      name: 'addressAdd',
      component: addressAdd
    },{
      path: '/pages/category/payResult',
      name: 'payResult',
      component: payResult
    },{
      path: '/pages/category/selCoupon',
      name: 'selCoupon',
      component: selCoupon
    },{
      path: '/pages/category/addressList',
      name: 'addressList2',
      component: addressList2
    },{
      path: '/pages/ucenter/addressAdd',
      name: 'addressAdd2',
      component: addressAdd2
    },{
      path: '/pages/ucenter/amountMoney',
      name: 'amountMoney',
      component: amountMoney
    },{
      path: '/pages/category/pay',
      name: 'pay',
      component: pay
    },{
      path: '/pages/register/register',
      name: 'register',
      component: register
    },{
      path: '/pages/xieyi/ptfwxy',
      name: 'ptfwxy',
      component: ptfwxy
    }
    
  ]
})


